/** Tech Profiles **/
.tech-profiles {
  // &:extend(.profile-category);
  // display: flex;
  // flex-wrap: wrap;
  padding-top: 2rem;
  padding-bottom: 2rem;

  profile {
    flex: 0 0 25%;
    margin-bottom: 1em;
  }

  @media screen and (max-width: 640px) {
    justify-content: center;

    profile {
      flex-basis: 30%;
    }
  }
}

.tech-profiles-category {
  //   display: flex;
  //   flex-wrap: wrap;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
}

.tech-profile {
}

.tech-profile-image-wrapper {
  flex: 1;
  margin-right: 2em;
}

.tech-profile-image {
  display: block;
  margin: 0 auto;
  width: 225px;
  height: 300px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

#certification ul {
  list-style: none;
  padding: 0;
}
#certification li {
  float: left;
  margin: 0 0 15px 10px;
  width: auto;
}
#certification img {
  max-width: 250px;
  width: auto;
  margin-bottom: 20px;
}

.tech-info {
  display: flex;
  margin-bottom: 2em;

  #profile-image {
    span {
    }
  }

  @media screen and (max-width: 640px) {
    flex-direction: column;
    justify-content: center;

    #profile-image {
      margin-bottom: 2em;
      text-align: left !important;

      img {
        height: 12em;
      }
    }
  }

  #tech-bio {
    flex: 3;
    .tech-name,
    .tech-title {
      color: #333;
    }

    .tech-name {
      margin-bottom: 0;
    }

    .tech-title {
      font-style: italic;
    }

    #profile-title {
      display: flex;
      flex-wrap: wrap;

      .profile-title-container {
        flex: 1;
        margin-right: 2em;
      }

      .profile-nn-stats-container {
        flex: 3;
        display: flex;
        align-items: center;

        > p {
          display: flex;
          flex-direction: column;
          margin: 0 1em 0 0;
          padding: 0.5em;
          border-radius: 10px;
          background-color: darken(#fff, 8%);
          min-width: 5em;

          span {
            text-align: center;
          }

          span:first-child {
            font-size: 1.25em;
            font-weight: bold;
          }
        }
      }
    }
  }
}

.tech-bio-atts {
  display: flex;
  flex-direction: column;

  > p {
    display: flex;
    margin: 5px 0 0 0;

    span {
      display: block;
      padding: 0.5em;
    }

    .bio-att-name {
      flex: 1;
      background-color: darken(#fff, 8%);
    }

    .bio-att-value {
      flex: 4;
      background-color: darken(#fff, 5%);
    }
  }
}
/* Profile Category */
.profile-category {
  padding: 2em 0;

  .image-frame {
    text-align: center;
    padding: 10px;

    a {
      display: inline-block;
      position: relative;
      z-index: 1;
      width: 225px;
      height: 300px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      .profile-image-overlay {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-position-y: 300px;
        opacity: 0;
        transition: opacity, 0.6s;
        box-shadow: inset -5px -5px 30px fade(#000, 75%);
      }

      &:hover {
        .profile-image-overlay {
          opacity: 1;
        }
      }

      // &:before {
      //     content: "";
      //     opacity: 0;
      //     position: absolute;
      //     z-index: 3;
      //     top: 0;
      //     left: 0;
      //     width: 100%;
      //     height: 100%;
      //     box-shadow: inset -5px -5px 30px fade(#000, 75%);
      //     transition: opacity 0.75s;
      // }

      // &:hover {
      //     &:before {
      //         opacity: 1;
      //     }
      // }
    }

    img {
      border: 1px solid #bbb;
      overflow: hidden;
      padding: 0;
      position: relative;
      z-index: 2;
      max-width: 100%;
      display: block;
      transition: all 0.5s linear;
    }
  }
}
