/* Shortcode */
.tech-imgage-container,
.tech-author {
  margin-bottom: 20px;
}
.tech-auth-contact,
.tech-auth-wrapper {
  width: 50%;
  padding-right: 2%;
  float: left;
}
.tech-auth-contact > ul {
  list-style: outside none none;
  margin: 0;
  padding: 0;
}

/* Horizontal Profile View */
.horiz-profile .tech-image-container {
  float: left;
}
.horiz-profile .tech-desc {
  float: right;
  width: 70%;
}

/* Media Query */
@media only screen and (max-width: 480px) {
  #tech-profiles .col-1-2,
  #tech-profiles .col-1-3,
  #tech-profiles .col-2-3,
  #tech-profiles .col-3-4,
  #tech-profiles .col-1-4,
  #tech-profiles .col-1-8,
  .horiz-profile .tech-image-container,
  .horiz-profile .tech-desc {
    float: none;
    width: 100%;
  }
  #profile-image > img {
    margin: 0 auto;
  }
  #tech-profiles .nn-combo-left {
    width: 100%;
  }
  #tech-profiles .nn-combo-right {
    display: none;
  }
  #certification li {
    float: none;
    margin-bottom: 10px;
  }
}

/* NN Fixes */
.single-profile {
  .nn-review-inner-cont {
    > h2,
    [itemprop='aggregateRating'],
    [itemprop='aggregateRating'] + div {
      display: none !important;
    }
  }
}
